<template>
  <div class="behavior-wrapper">
    <ul class="all-list-wrapper" v-if="list.length > 0">
      <li
        class="list-item"
        v-for="(item, index) in list"
        :key="index"
      >
        <span class="head-text">{{ desc }} :</span>
        <el-select class="short-select-width" v-model="item.callType" placeholder="" style="margin-right: 5px;">
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="2"></el-option>
        </el-select>
        <el-select
          class="short-select-width"
          v-if="item.callType === 1"
          v-model="item.nextSkillId"
          placeholder=""
          filterable
          @change="updateSkillPicked(item)"
        >
          <el-option
            v-for="skillItem in skillList"
            :key="skillItem.skillId"
            :label="skillItem.skillName"
            :value="Number(skillItem.skillId)"
          ></el-option>
        </el-select>
        <el-select
          class="short-select-width"
          v-else
          v-model="item.nextNodeId"
          placeholder=""
          @change="updateNodePicked(item)"
          filterable
        >
          <el-option
            v-for="nodeItem in childNodeList"
            :key="nodeItem.nodeId"
            :label="nodeItem.title"
            :value="nodeItem.nodeId"
          ></el-option>
        </el-select>
        <template v-if="item.callType === 1 && item.nextSkillId && getCurrentSkillItem(index).paramList.length > 0">
          <el-form-item
            v-for="(paramItem) in getCurrentSkillItem(index).paramList"
            :key="paramItem.paramKey"
            :label="paramItem.paramName"
          >
            <el-input
              style="margin-top: 5px;height: 30px;"
              :type="paramItem.dataType === 1 ? 'number': 'text'"
              v-model="item[paramItem.paramKey]"
            >
            </el-input>
          </el-form-item>
        </template>
        <el-button type="text" @click="del(index)" class="el-icon-close delete"></el-button>
      </li>
    </ul>
    <el-button type="text" @click="add">+{{ desc }}</el-button>
  </div>
</template>
<script>
export default {
  props: {
    isLimitOnly: { // 
      type: Boolean,
      default: false
    },
    desc: { // :  | 
      type: String,
      default: ''
    },
    skillList: { //  - 
      type: Array,
      default: () => []
    },
    childNodeList: { //  - 
      type: Array,
      default: () => []
    },
    list: { //  - 
      type: Array,
      default: () => []
    },
    parentIndex: { // list 
      type: Number,
      default: 0
    }
  },
  data () {
    return {
    }
  },
  computed: {
    //
  },
  watch: {
    //
  },
  methods: {
    getCurrentSkillItem (index) {
      const res = this.skillList.find(item => item.skillId.toString() === this.list[index].nextSkillId.toString()) || []
      return res
    },
    updateSkillPicked (payload) {
      console.debug('updateSkillPicked', payload)
    },
    updateNodePicked () {
      console.debug('updateNodePicked')
    },
    getNodeId (node) {
      const data = node.getData()
      if (data.nodeType === 3 || data.nodeType === 2 || data.nodeType === 1) {
        return data.nodeId
      } else if (data.type === 1 && data['button-type'] === 'dialogue-button') {
        return data.buttonId
      }
      return ''
    },
    add () { // 
      console.debug('add')
      this.$emit('add', { parentIndex: this.$props.parentIndex })
    },
    del (index) {
      console.debug('delete', index)
      this.$emit('del', { levelIndex: index, parentIndex: this.$props.parentIndex })
    }
  },
  mounted () {
    // console.debug('mounted nodeList: ', this.skillList)
  }
}
</script>
<style lang="stylus" scoped>
    .behavior-wrapper {
        // padding-left 10px
        .all-list-wrapper {
            .head-text {
                white-space nowrap
                margin-right 5px
                color #666
                font-size 14px
            }
            .list-item {
                display flex
                align-items center
                flex-wrap wrap
                .short-select-width {
                    width 120px
                    margin-bottom 5px
                    margin-left 5px
                }
                .delete {
                    color #666
                    font-size 14px
                    margin-left 10px
                }
            }
        }
    }

</style>
