<template>
  <div class="list-page-wrapper">
    <ul class="services-list-wrapper">
      <li
        class="list-item-wrapper"
        v-for="(item, index) in listArr"
        :key="index"
        @click="handleCommand('open_' + index)"
      >
        <img class="list-img" :src="item.img" alt="" />
        <div class="content-wrapper">
          <p class="title">{{ item.serviceName }}</p>
          <p class="desc">{{ item.serviceName }}</p>
        </div>
        <div class="more" @click.stop>
          <el-dropdown
            placement="bottom-end"
            trigger="click"
            :hide-on-click="true"
            @command="handleCommand"
          >
            <i class="el-icon-more"></i>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item :command="'open_' + index"></el-dropdown-item>
              <el-dropdown-item :command="'anylise_' + index"></el-dropdown-item>
              <el-dropdown-item :command="'withDraw_' + index"></el-dropdown-item>
              <el-dropdown-item :command="'delete_' + index"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </li>
      <li class="list-item-wrapper add-wrapper" @click="add">
        <i class="el-icon-plus add-icon"></i>
        <p class="add-desc"></p>
      </li>
    </ul>
    <el-dialog
      title=""
      :visible.sync="showAddServiceDialog"
      width="50%"
      :close-on-click-modal="false"
      @close="closeAddServiceDialog"
    >
      <el-form :model="servicesForm" :rules="rule" ref="servicesForm">
        <el-form-item label="" label-width="150" prop="serviceName">
          <el-input v-model="servicesForm.serviceName" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="150">
          <el-input
            autocomplete="off"
            :rows="5"
            v-model="servicesForm.remark"
            placeholder=""
            type="textarea"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showAddServiceDialog = false"> </el-button>
        <el-button type="primary" @click="confirmAddService"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { getServicesListAPI, addServicesAPI, deleteServicesAPI, offlineServiceAPI, getServiceAPI } from '@/services/services'
export default {
  data () {
    return {
      showAddServiceDialog: false,
      listArr: [],
      activeIndex: -1,
      servicesForm: {
        serviceName: '',
        remark: ''
      },
      rule: {
        serviceName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 50, message: ' 1  50', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    async initList () {
      try {
        const res = await getServicesListAPI()
        if (res.code === 1000 && res.data) {
          this.listArr = res.data.map((item) => {
            return {
              ...item,
              img: 'https://images.pexels.com/photos/4321069/pexels-photo-4321069.jpeg?auto=compress&cs=tinysrgb&w=800'
            }
          })
        }
      } catch (error) {
        console.error('initList error', error)
      }
    },
    openService () {
      console.log('openService')
      const targetTabInfo = this.listArr[this.activeIndex]
      this.$store.commit('services/addTab', targetTabInfo)
      this.$router.push({
        path: '/interaction-detail',
        query: {
          mode: 'edit'
        }
      })
    },
    anylise () {
      // console.log('anylise')
      const targetTabInfo = this.listArr[this.activeIndex]
      // console.debug('anylise targetTabInfo', targetTabInfo)
      this.$store.commit('services/addTab', targetTabInfo)
      this.$router.push({
        path: '/interaction-detail',
        query: {
          mode: 'anylise'
        }
      })
    },
    async withDraw () {
      console.log('withDraw')
      try {
        const res = await offlineServiceAPI({
          id: Number(this.listArr[this.activeIndex].id)
        })
        console.debug('withDraw res', res)
        if (res.code === 1000) {
          this.initList()
          this.$message({
            type: 'success',
            message: res.msg
          })
        } else {
          console.error('withDraw code !== 0', res)
        }
      } catch (error) {
        console.error('withDraw error', error)
      }
    },
    async deleteService () {
      try {
        const res = await deleteServicesAPI({
          id: Number(this.listArr[this.activeIndex].id)
        })
        console.debug('deleteServicesAPI res', res)
        if (res.code === 1000) {
          this.initList()
          this.$message({
            type: 'success',
            message: '!'
          })
        } else {
          console.error('deleteServicesAPI code !== 0', res)
        }
      } catch (error) {
        console.error('deleteServicesAPI error', error)
      }
      // confirm
      // const h = this.$createElement
      // this.$msgbox({
      //   title: '',
      //   closeOnClickModal: false,
      //   message: h('div', null, [
      //     h('p', null, [
      //       h('i', { class: 'el-icon-warning', style: 'color: #faad14;margin-right: 5px' }, ''),
      //       h('span', null, ',  ')
      //     ]),
      //     h('p', null, 'v1 '),
      //     h('p', null, 'v2'),
      //     h('p', null, 'v3 ')
      //   ]),
      //   showCancelButton: true,
      //   confirmButtonText: '',
      //   cancelButtonText: ''
      // }).then(async action => {
      //   this.$message({
      //     type: 'info',
      //     message: 'action: ' + action
      //   })
      // })
    },
    handleCommand (commandStr) {
      console.debug('handleCommand', commandStr)
      this.activeIndex = commandStr.split('_')[1]
      const command = commandStr.split('_')[0]
      switch (command) {
        case 'open':
          this.openService()
          break
        case 'anylise':
          this.anylise()
          break
        case 'withDraw':
          this.withDraw()
          break
        case 'delete':
          this.deleteService()
          break
        default:
          this.openService()
          break
      }
    },
    add () {
      this.showAddServiceDialog = true
    },
    confirmAddService () {
      this.$refs.servicesForm.validate(async valid => {
        if (valid) {
          this.showAddServiceDialog = false
          const res = await addServicesAPI(this.servicesForm)
          console.debug('addServicesAPI res', res)
          if (res.code === 1000) {
            this.initList()
            this.$message({
              type: 'success',
              message: '!'
            })
          }
        } else {
          return false
        }
      })
    },
    closeAddServiceDialog () {
      this.servicesForm = {
        serviceName: '',
        remark: ''
      }
    }
    // async getFirstService (payload) {
    //   try {
    //     const res = await getServiceAPI(payload)
    //     console.debug('getFirstService res', res)
    //   } catch (error) {
    //     console.error('getFirstService error', error)
    //   }
    // }
  },
  async created () {
    await this.initList()
  }
}
</script>
<style lang="stylus" scoped>
ul {
    margin-block-start 0
    margin-block-end 0
    padding-inline-start 0
    padding-inline-end 0
    li {
        list-style none
    }
}
.list-page-wrapper {
    width 100%
    min-height 100%
    background-color #fff
    padding 20px
    box-sizing border-box
    position relative
    .services-list-wrapper {
        display flex
        flex-wrap wrap
        flex-direction row
        .list-item-wrapper {
            width 250px
            height 300px
            box-shadow 0 2px 12px 0 rgba(0,0,0,.1)
            margin-right 30px
            margin-bottom 30px
            display flex
            flex-direction column
            position relative
            cursor pointer
            &.add-wrapper {
                border 1px dotted #999
                display flex
                justify-content center
                align-items center
                flex-direction column
                cursor pointer
                .add-icon {
                    font-size 60px
                    color #999
                    margin-bottom 20px
                }
                .add-desc {
                    font-size 15px
                    color #999
                }
            }
            .list-img {
                width 100%
                height 150px
            }
            .content-wrapper {
                width 100%
                flex 1
                padding 20px
                box-sizing border-box
                .title {
                    font-size 15px
                    color #000
                    font-family PingFangSC-Medium
                    font-weight 500
                }
                .desc {
                    font-size 12px
                    color #999
                    font-family PingFangSC-Regular
                    font-weight 400
                }
            }
            .more {
                position absolute
                right 10px
                bottom 10px
                width 20px
                height 20px
                display block
                cursor pointer
                i {
                    font-size 20px
                    color #999
                    transform rotate(90deg)
                }
            }
        }
    }
}
</style>
